export default {
  name: "reportWayConfig",
  data() {
    return {
      wx: "",
      money: "",
      id: "",
      keys: "",
      form: {
        wx: "",
        money: ""
      },
      rules: {
        wx: [{ required: true, message: "必填项不可以为空", trigger: "blur" }],
        money: [
          { required: true, message: "必填项不可以为空", trigger: "blur" }
        ]
      }
    };
  },
  async created() {
    let { data } = await this.$http.HallFunConfig.GetComplaintConfig({
      key: "complaint_config.lua"
    });
    // console.log(data);
    this.id = data.data[0].id;
    this.keys = data.data[0].sys_key;
    let res = JSON.parse(data.data[0].sys_val);
    console.log(res);
    this.form.wx = res[0].wx;
    this.form.money = res[0].money;
  },
  methods: {
    async save() {
      // console.log(this.form);
      if(this.form.wx === '' || this.form.money === ''){
         this.$message({
          type: "warning",
          message: "请完整填写信息!"
        });
        return false;
      }
      
      let { data } = await this.$http.HallFunConfig.PutComplaintConfig({
        keys: this.keys,
        id: this.id,
        values: JSON.stringify([this.form])
      });
      // console.log(data);
      if(data.code === 1 && data.msg === "ok"){
         this.$message({
          type: "success",
          message: "保存成功!"
        });
      }else {
        this.$message({
          type: "warning",
          message: "保存失败!"
        });
      }
    }
  }
};



